.chart-bar {
	display: flex;
	flex-direction: column;
	align-items: center;
	height: 100%;
	&__inner {
		width: 100%;
		height: 100%;
		border: 1px solid #313131;
		background-color: #c3b4f3;
		border-radius: 12px;
		display: flex;
		overflow: hidden;
		flex-direction: column;
		justify-content: flex-end;
	}
	&__fill {
		background-color: #4826b9;
		width: 100%;
		transition: all 0.3s ease-out;
	}
	&__label {
		font-weight: bold;
		text-align: center;
		font-size: 0.5rem;
	}
}
